@use "utilities" as *;

/* 
REVIEW COMPENDIUM TABLES
*/

.reviews-compendium {
    .page-header {
        display: flex;
        flex-direction: column;
        gap: var(--space-s);
        margin-block-end: var(--space-m);

        p {
            color: var(--clr-text-900);
            max-width: 60ch;
        }
    }

    &__toolbar {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
        gap: var(--space-s);
        margin-block-end: var(--space-xl);

        select {
            width: 100%;
            max-width: 22rem;
        }

        p {
            font-size: var(--step--2);
            font-family: var(--font-family-headings);
            color: var(--clr-text-900);
        }
    }

    .reviews-era {
        position: relative;
        scroll-margin-top: 6rem;

        // Add more vertical spacing between eras on mobile
        @media (max-width: 768px) {
            margin-block-end: var(--space-xl);
        }

        h2 {
            font-size: var(--step-2);
            margin-block-end: var(--space-xs);
            margin-inline-start: calc(var(--space-3xl) - var(--space-2xs));

            @media (max-width: 768px) {
                margin-inline-start: calc(
                    var(--space-xl) + var(--space-xs)
                ); // Less offset on mobile
            }
        }
    }

    .table-wrap {
        position: relative;
        width: 100%;
        background: var(--clr-surface-600);
        border: 1px solid var(--clr-border-darker);
        border-radius: var(--radius-small);
        overflow-x: auto;
        z-index: 1;
        box-sizing: border-box;

        // Add mobile-friendly scrolling indicator
        &::after {
            content: "â† Scroll to view more â†’";
            display: block;
            text-align: center;
            padding: var(--space-2xs);
            font-size: var(--step--2);
            color: var(--clr-text-900);
            background: hsl(215, 50%, 5%, 0.5);
            position: sticky;
            right: 0;
            bottom: 0;
            width: 100%;
        }

        // Hide scrolling indicator on larger screens
        @media (min-width: 768px) {
            &::after {
                display: none;
            }
        }
    }

    .reviews-table {
        width: 100%;
        min-width: 100%;
        font-size: var(--step--1);
        color: var(--clr-text-400);
        text-align: left;
        border-collapse: collapse;
        table-layout: fixed; // Forces the table to respect column widths exactly
        box-sizing: border-box;

        // For narrow screens, set a minimum table width to ensure proper column sizes
        @media (max-width: 768px) {
            min-width: 650px; // Minimum width to ensure all columns are reasonably sized on mobile
        }

        // Apply bottom border to all rows
        thead,
        tbody {
            tr {
                border-bottom: 1px solid var(--clr-border-darker);
            }
        }

        thead {
            th {
                font-family: var(--font-family-headings);
                color: var(--clr-text-900);
                background: hsl(216, 49%, 6%);
            }
        }

        // Subhead row (reviewer names under Scores colgroup)
        thead .subhead th {
            background: var(--clr-surface-600);
        }

        // Apply consistent padding to all table cells
        th,
        td {
            padding: calc(var(--space-2xs) + 0.2rem) var(--space-s);

            &:not(:last-child) {
                border-right: 1px solid var(--clr-border-darker);
            }

            // Adjust padding on mobile for better use of space
            @media (max-width: 768px) {
                padding: var(--space-2xs) calc(var(--space-xs) - 0.2rem);
                font-size: var(--step--2); // Slightly smaller text on mobile
            }
        }

        // Remove/override any previous cell padding rules
        tbody {
            th,
            td {
                vertical-align: middle;
            }
        }

        // Column width settings - responsive
        th:first-child,
        td:first-child {
            // Story column (first column) - exactly 50% of table width on desktop
            width: 50%;

            @media (max-width: 768px) {
                width: 45%; // Slightly narrower on mobile for better column balance
            }
        }

        th:nth-child(2),
        td:nth-child(2) {
            // Podcast episode column
            width: 16.67%;

            @media (max-width: 768px) {
                width: 25%; // Wider on mobile for better readability of episode numbers
            }
        }

        th:nth-child(3),
        td:nth-child(3),
        th:nth-child(4),
        td:nth-child(4) {
            // Score columns
            width: 16.67%;

            @media (max-width: 768px) {
                width: 15%; // Slightly narrower on mobile
            }
        }

        // Make sure the "Scores" header spans both score columns properly
        thead tr:first-child th[colspan="2"] {
            text-align: center;
        }

        // Zebra striping for readability (resets per tbody/season)
        tbody tr:nth-child(odd):not(.season-row) {
            background: hsl(216, 50%, 4%);
        }
        tbody tr:nth-child(even):not(.season-row) {
            background: hsl(216, 49%, 6%);
        }

        // Season header row
        .season-row th {
            color: var(--clr-brand-secondary);
            background: hsl(42, 91%, 58%, 0.03);

            @media (max-width: 768px) {
                font-size: var(--step--1); // Keep season headers more prominent
                padding-block: calc(
                    var(--space-xs) - 0.2rem
                ); // More vertical space
            }
        }

        // Make sure the season header spans across columns properly
        .season-row th[colspan="2"] {
            text-align: left;
        }

        // Story title column
        .cell--story {
            word-break: normal;
            hyphens: auto;
            overflow: hidden;
            text-overflow: ellipsis;
        }

        // Score cells
        .cell--score {
            font-variant-numeric: tabular-nums;
            white-space: nowrap;
        }

        // Podcast links
        .cell--pod {
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;

            a {
                font-weight: var(--fw-semibold);
                color: var(--color-link, #007acc);
                text-decoration: none;

                &:hover,
                &:focus {
                    text-decoration: underline;
                }
            }
        }

        // Remove border from only the very last row in the table (last tr of last tbody)
        > tbody:last-of-type > tr:last-of-type {
            border-bottom: none;
        }

        // Remove border from last cell in each row
        th:last-child,
        td:last-child {
            border-right: none;
        }
    }

    // Doctor / Spin-off image "behind" the table
    .era-image {
        position: absolute;
        top: -1rem;
        left: 1rem;
        width: 80px;
        height: auto;
        pointer-events: none;
        z-index: 0;

        @media (max-width: 768px) {
            width: 60px; // Smaller image on mobile
            top: -0.5rem;
            left: 0.5rem;
            opacity: 0.8; // Slightly more transparent on mobile
        }
    }
}
