/* 
FONTS
Self-hosted font details.
*/

@font-face {
    font-family: "Lora";
    font-weight: 400;
    font-style: normal;
    src: url("fonts/Lora-Regular.woff2") format("woff2");
    font-display: swap;
}

@font-face {
    font-family: "Lora";
    font-weight: 400;
    font-style: italic;
    src: url("fonts/Lora-Italic.woff2") format("woff2");
    font-display: swap;
}

@font-face {
    font-family: "Lora";
    font-weight: 600;
    font-style: normal;
    src: url("fonts/Lora-SemiBold.woff2") format("woff2");
    font-display: swap;
}

@font-face {
    font-family: "Lora";
    font-weight: 600;
    font-style: italic;
    src: url("fonts/Lora-SemiBoldItalic.woff2") format("woff2");
    font-display: swap;
}

